export default {
	data() {
		return {
			nickName:"昵称",
			avatarUrl:"/static/logo.png"
		}
	},
	methods: {
		mylogin() {
			console.log(11)
			uni.login({
				provider:"weixin",
				success: (res) => {
					//console.log(res)
					uni.getUserInfo({
						provider:"weixin",
						success: (result) => {
							console.log(result.userInfo);
							this.nickName = result.userInfo.nickName;
							this.avatarUrl = result.userInfo.avatarUrl;
						}
					})
				}
			})
			console.log(222)
		}
	},
	onLoad() {
		this.mylogin();
	}
}
